*, *:before, *:after {
  box-sizing: border-box;
}

.container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 300px) {
  #actamofis01 {
    width: 100%;
    padding: 0;
  }
  #actamofis01 .container {
    flex-direction: column;
    background-color: #fff;
    box-shadow: 0px 0px 27px rgba(0, 0, 0, 0.1);
  }
  #actamofis01 .tabs {
    display: none;
  }
  #actamofis01 .tab {
    float: left;
    /* Style the buttons inside the tab */
  }
  #actamofis01 .tab button {
    display: block;
    background-color: inherit;
    padding: 1.4rem;
    width: 100%;
    border: none;
    outline: none;
    text-align: left;
    cursor: pointer;
    font-size: 17px;
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1.5rem;
    color: #054877;
    /* Change background color of buttons on hover */
    /* Create an active/current "tab button" class */
  }
  #actamofis01 .tab button:hover {
    background-color: #ddd;
  }
  #actamofis01 .tab button.active {
    background-color: #c6e5f7;
  }
  #actamofis01 .tablinks {
    transition: 500ms;
  }
  #actamofis01 .tablinks.mobile {
    background-color: #013b65;
    color: #ffffff;
    display: block;
    font-family: "Open Sans", sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.5;
    padding: 1rem 1.4rem;
    text-align: left;
    text-transform: uppercase;
    width: 100%;
  }
  #actamofis01 .tablinks.mobile span.closed {
    display: inline-block;
    margin-right: 2px;
  }
  #actamofis01 .tablinks.mobile span.open {
    display: none;
  }
  #actamofis01 .tablinks.mobile.active {
    background-color: #298dd3;
  }
  #actamofis01 .tablinks.mobile.active span.closed {
    display: none;
  }
  #actamofis01 .tablinks.mobile.active span.open {
    display: inline-block;
    margin-right: 2px;
  }
  #actamofis01 .tabcontent {
    display: none;
    border-left: none;
    background-color: #fff;
  }
  #actamofis01 .tabcontent.active {
    display: block;
  }
  #actamofis01 .itemC {
    display: flex;
    flex-direction: column;
  }
  #actamofis01 .itemC h1 {
    font-family: "Open Sans", sans-serif;
    font-size: 20px;
    font-weight: 650;
    margin-bottom: 0;
  }
  #actamofis01 .itemC p {
    font-family: "Open Sans", sans-serif;
    font-size: 20px;
    margin-top: 0em;
    margin-bottom: 0;
    font-style: italic;
  }
  #actamofis01 .itemC .badge {
    padding: 0 2em;
    margin-bottom: 10px;
  }
  #actamofis01 .itemC .member-photo {
    width: 100%;
    padding-top: 100%;
    background-position: center top;
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.1);
  }
  #actamofis01 .owl-carousel {
    padding-bottom: 12px;
  }
  #actamofis01 .owl-theme .owl-dots .owl-dot span {
    width: 1.5rem;
    height: 1.5rem;
    margin: 5px;
    background: #298dd3 !important;
  }
  #actamofis01 .owl-theme .owl-dots .owl-dot.active span {
    background: #013a67 !important;
  }
  #member010 .member-photo,
  #member010_modal .member-photo {
    background-image: url(../../img/ppl/tom.jpg);
  }
  #member020 .member-photo,
