@media screen and (min-width: 650px) {
  #actamofis01 {
    /* Style the tab content */
  }
  #actamofis01 .container {
    width: 100%;
    display: flex;
    background-color: transparent;
    box-shadow: none;
  }
  #actamofis01 .tabs {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-bottom: 20px;
    flex-direction: row;
    align-items: center;
  }
  #actamofis01 .tab span, #actamofis01 .tab button {
    font-weight: 700;
  }
  #actamofis01 .tab span {
    font-weight: 900;
    margin: 0 1em;
    font-family: "Open Sans", sans-serif;
  }
  #actamofis01 .tab button {
    width: auto;
    font-size: 16px;
    padding: 7px 0 5px;
    color: #054877;
  }
  #actamofis01 .tab button.active {
    background-color: transparent;
    color: #116fb0;
    border-bottom: 2px solid #308dca;
  }
  #actamofis01 .pages {
    width: 100%;
  }
  #actamofis01 .tablinks.mobile {
    display: none;
  }
  #actamofis01 .exowlbox {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 1em auto;
    opacity: 1;
  }
  #actamofis01 .exowlbox:after {
    content: "";
    flex-basis: 23%;
    display: block;
  }
  #actamofis01 .itemC {
    width: 23%;
    box-sizing: border-box;
    margin-bottom: 3em;
    position: relative;
  }
  #actamofis01 .itemC h1, #actamofis01 .itemC p {
    font-size: 20px;
  }
  #actamofis01 .itemC img {
    width: 100%;
    box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.1);
  }
  #actamofis01 .itemC .badge {
    padding: 0;
  }
  #actamofis01 .itemC .cross {
    width: 30px;
    position: absolute;
    top: 10px;
    right: 10px;
  }
  #actamofis01 .itemC .cross div {
    height: 30px;
    position: relative;
    background-color: #cccccc;
    border-radius: 12%;
    padding: 1%;
  }
  #actamofis01 .itemC .cross div:before {
    left: 50%;
    width: 20%;
    margin-left: -10%;
    height: 60%;
    top: 20%;
  }
  #actamofis01 .itemC .cross div:after {
    top: 50%;
    height: 20%;
    margin-top: -10%;
    width: 60%;
    left: 20%;
  }
  #actamofis01 .itemC .cross div:before, #actamofis01 .itemC .cross div:after {
    content: "";
    position: absolute;
    z-index: 1;
    background: #ffffff;
  }
  #actamofis01 .tabcontent {
    display: none;
    background-color: transparent;
  }
  #actamofis01 .tabcontent.active {
    display: flex;
  }
}
