@media screen and (min-width: 300px) {
  #tafis_about_page .tafis-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 2rem;
  }
  #tafis_about_page .tafis--buttons, #tafis_about_page .tafis--frames-images {
    display: none;
  }
  #tafis_about_page .owl-item {
    transition-duration: 5s;
  }
  #tafis_about_page .tafis--images {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  #tafis_about_page .tafis--images-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    width: 27rem;
  }
  #tafis_about_page .tafis--images-wrapper:after {
    content: "";
    width: 100%;
    height: 3px;
    box-shadow: #333 1px 1px 8px 10px;
    position: absolute;
    bottom: 0px;
    left: -5px;
    border-radius: 50%;
    background-color: #333;
  }
  #tafis_about_page .tafis--image {
    display: none;
    background-size: contain;
    background-repeat: no-repeat;
    width: 100%;
    padding-bottom: 43rem;
  }
  #tafis_about_page .tafis--image.active {
    display: flex;
    z-index: 1;
  }
  #tafis_about_page .owl-carousel .owl-stage-outer {
    padding-bottom: 1em;
  }
  #tafis_about_page .owl-carousel .owl-stage {
    display: flex;
  }
  #tafis_about_page .tafis--frame {
    display: flex;
    align-items: center;
    padding: 0em 1em;
    background-repeat: no-repeat;
    background-position: left;
    background-image: url(../../img/about-us-gradient-clear-left.png);
  }
  #tafis_about_page .tafis--frame h1 {
    font-size: 26px;
    font-family: "Open Sans", sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    line-height: 1.4em;
  }
  #tafis_about_page .tafis--frame p, #tafis_about_page .tafis--frame li {
    font-family: "Merriweather", serif;
    font-size: 20px;
    line-height: 1.4em;
  }
  #tafis_about_page .tafis--frame li {
    list-style: none;
    margin-top: 1rem;
  }
  #tafis_about_page .tafis--frame li i {
    color: #6ec1e4;
    padding-right: 1rem;
    margin-left: -2.6rem;
    font-size: 25px;
  }
  #tafis_about_page .tafis--frame a {
    font-family: "Open Sans", sans-serif;
    color: #054877;
    text-decoration: none;
    font-weight: 700;
  }
  #tafis_about_page .tafis--frame a:hover {
    text-decoration: underline;
  }
  #tafis_about_page .tafis--frame .icon {
    background-size: contain;
    background-repeat: no-repeat;
    min-width: 40%;
  }
  #tafis_about_page .frame-content {
    width: 100%;
    box-shadow: rgba(0, 0, 0, 0.05) 3px 3px 12px 6px;
    background-color: #fff;
    padding: 2em 3em;
  }
  #tafis_about_frame_01 .icon {
    background-image: url(../../img/about-us-01.png);
  }
  #tafis_about_frame_02 .icon {
    background-image: url(../../img/about-us-02.png);
  }
  #tafis_about_frame_03 .icon {
    background-image: url(../../img/about-us-03.png);
  }
}

@media screen and (min-width: 650px) {
  #tafis_about_page .tafis--buttons {
    white-space: normal;
    display: flex;
    flex-direction: row;
  }
  #tafis_about_page .tafis--buttons a {
    text-decoration: none;
  }
  #tafis_about_page .tafis--buttons button, #tafis_about_page .tafis--buttons a {
    text-transform: uppercase;
    font-family: "Open Sans", sans-serif;
    font-size: 16px;
    border: transparent;
    background-color: transparent;
    cursor: pointer;
    color: #054877;
    font-weight: bold;
    padding: 7px 0 5px;
    border-bottom: 2px solid transparent;
    transition: all 0.3s ease-in-out;
  }
  #tafis_about_page .tafis--buttons button:hover, #tafis_about_page .tafis--buttons a:hover {
    color: #308dca;
  }
  #tafis_about_page .tafis--buttons button.active, #tafis_about_page .tafis--buttons a.active {
    color: #308dca;
    border-bottom: 2px solid #308dca;
  }
  #tafis_about_page .tafis--buttons span {
    font-weight: 900;
    margin: 0 1em 1px;
    font-family: "Open Sans", sans-serif;
  }
  #tafis_about_page .tafis--frames-images {
    display: flex;
    width: 100%;
    margin-top: 3rem;
    flex-direction: column;
    justify-content: center;
  }
  #tafis_about_page .tafis--images {
    align-items: center;
    margin-top: 2em;
  }
  #tafis_about_page .owl-theme .owl-dots .owl-dot span {
    width: 1.5rem;
    height: 1.5rem;
    margin: 5px 2px;
    background: #308dca !important;
  }
  #tafis_about_page .owl-theme .owl-dots .owl-dot.active span {
    background: #47aff6 !important;
  }
  #tafis_about_page .tafis--frame {
    background-position: center 5%;
    flex-direction: column;
  }
  #tafis_about_page .tafis--frame .icon {
    width: 300px;
    height: 350px;
  }
  #tafis_about_page .tafis--frame .frame-content {
    margin-left: 0;
  }
  #tafis_about_page .tafis--frame h1 {
    display: block;
  }
  #tafis_about_page .tafis--frame.reverse {
    background-position: center 5%;
  }
  #tafis_about_page .tafis--frame.reverse .icon {
    order: 1;
  }
  #tafis_about_page .tafis--frame.reverse .frame-content {
    order: 2;
  }
}

@media screen and (min-width: 800px) {
  #tafis_about_page .tafis--buttons {
    font-size: inherit;
    align-items: center;
  }
  #tafis_about_page .tafis--frames-images {
    flex-direction: row;
  }
  #tafis_about_page .tafis--images {
    margin-top: 0;
  }
}

@media screen and (min-width: 1200px) {
  #tafis_about_page .tafis--frame {
    margin: 0 4rem 0;
    flex-direction: row;
    background-position: left;
  }
  #tafis_about_page .tafis--frame .frame-content {
    margin-left: 4%;
    min-width: 55%;
  }
  #tafis_about_page .tafis--frame.reverse {
    background-image: url(../../img/about-us-gradient-clear-right.png);
    background-position: right;
  }
  #tafis_about_page .tafis--frame.reverse .icon {
    order: 2;
  }
  #tafis_about_page .tafis--frame.reverse .frame-content {
    order: 1;
    margin-left: 0;
    margin-right: 4%;
  }
  #tafis_about_page .tafis--frame .icon {
    width: 600px;
    height: 600px;
  }
}
