#tafis_tour_page .tafis-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2rem;
}

#tafis_tour_page .tafis-container .owl-dots {
  display: none;
}

#tafis_tour_page .tafis-container .tafis--buttons {
  white-space: normal;
  display: flex;
  flex-direction: row;
}

#tafis_tour_page .tafis-container .tafis--buttons a {
  text-decoration: none;
}

#tafis_tour_page .tafis-container .tafis--buttons button, #tafis_tour_page .tafis-container .tafis--buttons a {
  text-transform: uppercase;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  border: transparent;
  background-color: transparent;
  cursor: pointer;
  color: #054877;
  font-weight: bold;
  padding: 7px 0 5px;
  border-bottom: 2px solid transparent;
  transition: all 0.3s ease-in-out;
}

#tafis_tour_page .tafis-container .tafis--buttons button:hover, #tafis_tour_page .tafis-container .tafis--buttons a:hover {
  color: #308dca;
}

#tafis_tour_page .tafis-container .tafis--buttons button.active, #tafis_tour_page .tafis-container .tafis--buttons a.active {
  color: #308dca;
  border-bottom: 2px solid #308dca;
}

#tafis_tour_page .tafis-container .tafis--buttons span {
  font-weight: 900;
  margin: 0 1em 1px;
  font-family: "Open Sans", sans-serif;
}

#tafis_tour_page .tafis-container .tafis--frames-images {
  display: flex;
  flex-direction: row;
}

#tafis_tour_page .tafis-container .owl-item {
  transition-duration: 5s;
}

#tafis_tour_page .tafis-container .tafis--frame {
  margin: 2rem 1rem 0;
}

#tafis_tour_page .tafis-container .tafis--frame h1 {
  font-size: 26px;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 1.4em;
  text-align: center;
}

#tafis_tour_page .tafis-container .tafis--frame p, #tafis_tour_page .tafis-container .tafis--frame li {
  font-family: "Merriweather", serif;
  font-size: 20px;
  line-height: 1.4em;
}

#tafis_tour_page .tafis-container .tafis--frame ul {
  padding-left: 32px;
  list-style: none;
  display: flex;
  flex-direction: column;
}

#tafis_tour_page .tafis-container .tafis--frame li {
  margin-bottom: 1rem;
  display: flex;
}

#tafis_tour_page .tafis-container .tafis--frame li:before {
  font-family: 'Font Awesome 5 Free';
  content: '\f0a9';
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding-right: 14px;
  margin-left: -30px;
  font-size: 24px;
  vertical-align: middle;
  font-weight: 900;
  color: #298dd3;
}

#tafis_tour_page .tafis-container .tafis--frame li i {
  padding-left: 31px;
  color: #298dd3;
  padding-right: 1rem;
  margin-left: -2.6rem;
  font-size: 25px;
}

#tafis_tour_page .tafis-container .tafis--images {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#tafis_tour_page .tafis-container .tafis--images-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  width: 27rem;
}

#tafis_tour_page .tafis-container .tafis--images-wrapper:after {
  content: "";
  width: 100%;
  height: 3px;
  box-shadow: #333 1px 1px 8px 10px;
  position: absolute;
  bottom: 0px;
  left: -5px;
  border-radius: 50%;
  background-color: #333;
}

#tafis_tour_page .tafis-container .tafis--image {
  display: none;
  background-size: contain;
  background-repeat: no-repeat;
  width: 100%;
  padding-bottom: 43rem;
}

#tafis_tour_page .tafis-container .tafis--image.active {
  display: flex;
  z-index: 1;
}

#tafis_tour_page .tafis-container .tafis--accordion-item {
  display: none;
  margin: 0 auto;
}

#tafis_tour_page .tafis-container .tafis--accordion-item h1 {
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
}

#tafis_tour_page .tafis-container .tafis--accordion-item p, #tafis_tour_page .tafis-container .tafis--accordion-item li {
  font-family: "Merriweather", serif;
  font-size: 18px;
  line-height: 1.4em;
}

#tafis_tour_page .tafis-container .tafis--accordion-item.active {
  display: block;
}

#tafis_tour_page .owl-carousel .owl-stage-outer {
  padding-bottom: 1em;
}

#tafis_tour_page .owl-carousel .owl-stage {
  display: flex;
}

#tafis_tour_page .owl-item {
  background-color: #ffffff;
  box-shadow: #ccc 1px 1px 8px 2px;
}

#tafis_tour_page .tafis--frame {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2em 1em;
}

#tafis_tour_page .tafis--frame .icon {
  background-size: contain;
  background-repeat: no-repeat;
  width: 10em;
  height: 10em;
}

#tafis_tour_page .frame-content {
  width: 100%;
}

#tafis_tour_page #tafis-frame_01 .icon {
  background-image: url(../../img/icon01.png);
}

#tafis_tour_page #tafis-frame_02 .icon {
  background-image: url(../../img/icon02.png);
}

#tafis_tour_page #tafis-frame_03 .icon {
  background-image: url(../../img/icon03.png);
}

#tafis_tour_page #tafis-frame_04 .icon {
  background-image: url(../../img/icon04.png);
}

#tafis_tour_page #tafis-frame_05 .icon {
  background-image: url(../../img/icon05.png);
}

@media screen and (min-width: 300px) {
  #tafis_tour_page .tafis-container .tafis--buttons, #tafis_tour_page .tafis-container .tafis--frames-images {
    display: none;
  }
}

@media screen and (min-width: 650px) {
  #tafis_tour_page .tafis-container .tafis--frame h1 {
    display: block;
  }
  #tafis_tour_page .tafis-container .tafis--buttons {
    display: flex;
    flex-direction: row;
    white-space: normal;
    align-items: center;
    padding: 0 4em;
  }
  #tafis_tour_page .tafis-container .tafis--frames-images {
    display: flex;
    width: 100%;
    margin-top: 3rem;
    flex-direction: column;
    justify-content: center;
  }
  #tafis_tour_page .tafis-container .tafis--images {
    align-items: center;
    margin-top: 2em;
  }
  #tafis_tour_page .tafis-container .tafis--accordion {
    display: none;
    width: 100%;
    border: 1px teal;
  }
  #tafis_tour_page .tafis-container .owl-theme .owl-dots .owl-dot span {
    width: 1.5rem;
    height: 1.5rem;
    margin: 5px 2px;
    background: #308dca !important;
  }
  #tafis_tour_page .tafis-container .owl-theme .owl-dots .owl-dot.active span {
    background: #47aff6 !important;
  }
  #tafis_tour_page .tafis_area {
    width: 100%;
    padding: 0 4em;
  }
}

@media screen and (min-width: 800px) {
  #tafis_tour_page .tafis-container .tafis--buttons {
    font-size: inherit;
  }
  #tafis_tour_page .tafis-container .tafis--frames-images {
    flex-direction: row;
  }
  #tafis_tour_page .tafis-container .tafis--images {
    margin-top: 0;
  }
  #tafis_tour_page .tafis_area {
    padding: 0 2em;
  }
}

@media screen and (min-width: 1200px) {
  #tafis_tour_page .tafis-container .tafis--frame {
    margin: 2rem 4rem 0;
  }
}
