#tafis_home_page .tafis-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2rem;
}

#tafis_home_page .tafis-container .tafis--buttons button {
  text-transform: uppercase;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  border: transparent;
  background-color: transparent;
  cursor: pointer;
  color: #054877;
  font-weight: bold;
  padding: 7px 0 5px;
  border-bottom: 2px solid transparent;
  transition: all 0.3s ease-in-out;
}

#tafis_home_page .tafis-container .tafis--buttons button:hover {
  color: #308dca;
}

#tafis_home_page .tafis-container .tafis--buttons button.active {
  color: #308dca;
  border-bottom: 2px solid #308dca;
}

#tafis_home_page .tafis-container .tafis--buttons span {
  font-weight: 900;
  margin: 0 1em;
}

#tafis_home_page .tafis-container .tafis--frames-images {
  display: flex;
  flex-direction: row;
  z-index: 10;
}

#tafis_home_page .tafis-container .tafis {
  background-color: #fff;
  padding: 1rem;
  box-shadow: #ccc 1px 1px 14px 2px;
}

#tafis_home_page .tafis-container .owl-item {
  transition-duration: 5s;
}

#tafis_home_page .tafis-container .tafis--frame {
  margin: 0 auto;
}

#tafis_home_page .tafis-container .tafis--frame h1 {
  font-size: 26px;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 1em;
  margin-top: 0;
}

#tafis_home_page .tafis-container .tafis--frame p, #tafis_home_page .tafis-container .tafis--frame li {
  font-family: "Merriweather", serif;
  font-size: 20px;
  line-height: 1.4em;
}

#tafis_home_page .tafis-container .tafis--frame ul {
  padding-left: 40px;
}

#tafis_home_page .tafis-container .tafis--frame li {
  list-style: none;
  margin-top: 1rem;
}

#tafis_home_page .tafis-container .tafis--frame li i {
  color: #298dd3;
  padding-right: 15px;
  margin-left: -40px;
  font-size: 25px;
}

#tafis_home_page .tafis-container .tafis--images {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

#tafis_home_page .tafis-container .tafis--images-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  width: 324px;
  padding: 0 26px 0 30px;
  background-color: #ececec;
}

#tafis_home_page .tafis-container .tafis--images-wrapper:after {
  content: "";
  width: 94%;
  height: 17px;
  box-shadow: #333 0px 6px 8px 4px;
  position: absolute;
  bottom: 2px;
  left: 11px;
  border-radius: 50%;
  background-color: #333;
  z-index: -3;
}

#tafis_home_page .tafis-container .tafis--image {
  display: none;
  background-size: contain;
  background-repeat: no-repeat;
  width: 100%;
  padding-bottom: 43rem;
  margin-bottom: -1px;
}

#tafis_home_page .tafis-container .tafis--image.active {
  display: flex;
  z-index: 1;
}

#tafis_home_page .tafis-container .tafis--accordion-item {
  display: none;
  margin: 0 auto;
  color: #333333;
}

#tafis_home_page .tafis-container .tafis--accordion-item h1 {
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
}

#tafis_home_page .tafis-container .tafis--accordion-item p, #tafis_home_page .tafis-container .tafis--accordion-item li {
  font-family: "Merriweather", serif;
  font-size: 18px;
  line-height: 1.4em;
}

#tafis_home_page .tafis-container .tafis--accordion-item.active {
  display: block;
}

#tafis-image_01,
#tafis-image_02,
#tafis-image_03,
#tafis-image_04,
#tafis-image_05,
#tafis-image_06,
#tafis-image_07,
#tafis-image_08,
#tafis-image_09 {
  background-image: url(../../img/tafis-frame-img-01.png);
}

@media screen and (min-width: 300px) {
  #tafis_home_page .tafis-container .owl-dots {
    display: none;
  }
  #tafis_home_page .tafis-container .tafis--frame {
    margin: 2rem 1rem 0;
  }
  #tafis_home_page .tafis-container .tafis--accordion {
    width: 100%;
  }
  #tafis_home_page .tafis-container .tafis--accordion button {
    transition: 500ms;
    background-color: #013b65;
    color: #ffffff;
    display: block;
    font-family: "Open Sans", sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.5;
    padding: 1rem 1.4rem;
    text-align: left;
    text-transform: uppercase;
    width: 100%;
  }
  #tafis_home_page .tafis-container .tafis--accordion button span.closed {
    display: inline-block;
    margin-right: 2px;
  }
  #tafis_home_page .tafis-container .tafis--accordion button span.open {
    display: none;
  }
  #tafis_home_page .tafis-container .tafis--accordion button.active {
    background-color: #298dd3;
  }
  #tafis_home_page .tafis-container .tafis--accordion button.active span.closed {
    display: none;
  }
  #tafis_home_page .tafis-container .tafis--accordion button.active span.open {
    display: inline-block;
    margin-right: 2px;
  }
  #tafis_home_page .tafis-container .tafis--accordion-item {
    padding: 2rem;
    background-color: #ffffff;
  }
  #tafis_home_page .tafis-container .tafis--accordion-item ul {
    list-style: none;
    padding: 0;
  }
  #tafis_home_page .tafis-container .tafis--accordion-item li {
    margin-top: 1rem;
  }
  #tafis_home_page .tafis-container .tafis--accordion-item li:before {
    content: "\f0a9";
    color: #308dca;
    font-family: "Font Awesome\ 5 Free";
    font-weight: 900;
    margin-right: 6px;
    font-size: 20px;
  }
  #tafis_home_page .tafis-container .tafis--buttons, #tafis_home_page .tafis-container .tafis--frames-images {
    display: none;
  }
  #tafis_home_page .tafis-container .tafis--accordion {
    display: block;
  }
}

@media screen and (min-width: 650px) {
  #tafis_home_page .tafis-container {
    margin-right: 5%;
    margin-left: 5%;
  }
  #tafis_home_page .tafis-container .tafis--frame {
    padding: 1rem;
    margin: 1rem;
  }
  #tafis_home_page .tafis-container .tafis--frame h1 {
    display: block;
  }
  #tafis_home_page .tafis-container .tafis--buttons {
    display: flex;
    flex-direction: row;
    white-space: normal;
    align-items: center;
  }
  #tafis_home_page .tafis-container .tafis--frames-images {
    display: flex;
    width: 100%;
    margin-top: 3rem;
    flex-direction: column;
  }
  #tafis_home_page .tafis-container .tafis {
    max-width: 100%;
  }
  #tafis_home_page .tafis-container .tafis--images {
    align-items: center;
    margin-top: 2em;
  }
  #tafis_home_page .tafis-container .tafis--accordion {
    display: none;
    width: 100%;
    border: 1px teal;
  }
  #tafis_home_page .tafis-container .owl-theme .owl-dots .owl-dot span {
    width: 1.5rem;
    height: 1.5rem;
    margin: 5px 2px;
    background: #308dca !important;
  }
  #tafis_home_page .tafis-container .owl-theme .owl-dots .owl-dot.active span {
    background: #47aff6 !important;
  }
  #tafis_area {
    width: 100%;
  }
}

@media screen and (min-width: 800px) {
  #tafis_home_page .tafis-container .tafis--buttons {
    font-size: inherit;
  }
  #tafis_home_page .tafis-container .tafis {
    width: 59%;
  }
  #tafis_home_page .tafis-container .tafis--frames-images {
    flex-direction: row;
    justify-content: space-between;
  }
  #tafis_home_page .tafis-container .tafis--images {
    margin-top: 0;
    width: 273px;
  }
}

@media screen and (min-width: 960px) {
  #tafis_home_page .tafis-container .tafis {
    width: 62%;
  }
}

@media screen and (min-width: 1050px) {
  #tafis_home_page .tafis-container .tafis {
    width: 64%;
  }
}

@media screen and (min-width: 1200px) {
  #tafis_home_page .tafis-container .tafis {
    width: 68%;
  }
}

@media screen and (min-width: 1280px) {
  #tafis_home_page .tafis-container .tafis {
    width: 69%;
  }
}

@media screen and (min-width: 1366px) {
  #tafis_home_page .tafis-container .tafis {
    width: 71%;
  }
}
