#actamofis01 .modal {
  display: none;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 15;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
  color: #333333;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
}

#actamofis01 .modal .cross {
  width: 30px;
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 5;
}

#actamofis01 .modal .cross div {
  height: 30px;
  position: relative;
  background-color: #cccccc;
  border-radius: 12%;
  padding: 1%;
}

#actamofis01 .modal .cross div:before {
  left: 50%;
  width: 20%;
  margin-left: -10%;
  height: 60%;
  top: 20%;
}

#actamofis01 .modal .cross div:after {
  top: 50%;
  height: 20%;
  margin-top: -10%;
  width: 60%;
  left: 20%;
}

#actamofis01 .modal .cross div:before, #actamofis01 .modal .cross div:after {
  content: "";
  position: absolute;
  z-index: 1;
  background: #ffffff;
  transform: rotate(45deg);
}

#actamofis01 .modal-headshot {
  position: relative;
}

#actamofis01 .modal-content {
  display: flex;
  position: relative;
  background-color: #fefefe;
  margin: auto;
  padding: 0;
  border: 1px solid #888;
  width: 95%;
  max-width: 1350px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-name: animatetop;
  animation-duration: 0.4s;
}

@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}

@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}

#actamofis01 .close {
  color: #333333;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

#actamofis01 .close:hover,
#actamofis01 .close:focus {
  color: #333333;
  text-decoration: none;
  cursor: pointer;
}

#actamofis01 .modal-header {
  padding: 2px 16px;
  line-height: 2.1em;
}

#actamofis01 .modal-body {
  padding: 2px 16px;
  line-height: 1.5;
}

#actamofis01 .modal-footer {
  padding: 2px 16px;
  color: white;
}

@media screen and (min-width: 300px) {
  #actamofis01 .modal .member-photo {
    box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.2);
    height: 95%;
    width: 90%;
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
  }
  #actamofis01 .modal-content {
    flex-direction: column;
    align-items: center;
    height: 97vh;
    overflow-y: auto;
  }
  #actamofis01 .modal-headshot {
    width: 100%;
  }
  #actamofis01 .modal-headshot-background {
    background: #212531;
    background: linear-gradient(to right bottom, #054877 50%, transparent 50%);
    display: flex;
    font-size: 1.5em;
    text-align: center;
    color: #ffffff;
    font-weight: normal;
    text-transform: uppercase;
    font-family: sans-serif;
    padding-top: 100%;
    width: 100%;
  }
  #actamofis01 .modal-headshot-foreground {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    width: 100%;
  }
  #actamofis01 .modal-header {
    margin: 20px 0 5px;
  }
  #actamofis01 .modal-header h2, #actamofis01 .modal-header h3 {
    font-size: 20px;
    margin: 0;
  }
  #actamofis01 .modal-header h2 {
    font-weight: 700;
  }
  #actamofis01 .modal-header h3 {
    font-weight: 500;
    font-style: italic;
  }
  #actamofis01 .modal-body p {
    font-family: "Merriweather", serif;
    font-size: 18px;
  }
}

@media screen and (min-width: 568px) {
  #actamofis01 .modal-content {
    flex-direction: row;
    align-items: flex-start;
    max-height: 620px;
    overflow-y: unset;
  }
  #actamofis01 .modal-headshot {
    max-width: 35%;
  }
  #actamofis01 .modal-description {
    width: 100%;
    max-height: 620px;
    padding-right: 5%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
  #actamofis01 .modal-header {
    line-height: 2.6em;
  }
  #actamofis01 .modal-header h2, #actamofis01 .modal-header h3 {
    font-size: 34px;
  }
  #actamofis01 .modal-body {
    max-height: 59vh;
    margin-bottom: 5%;
    overflow-y: auto;
  }
  #actamofis01 .modal-body p {
    font-size: 20px;
  }
  #actamofis01 .modal .member-photo {
    height: 90%;
    width: 85%;
  }
  #actamofis01 .modal-headshot-background {
    padding-top: 130%;
  }
  #actamofis01 .modal-headshot-foreground {
    justify-content: center;
  }
}

@media screen and (min-width: 1440px) {
  #actamofis01 .modal-body {
    max-height: 450px;
  }
}
