*, *:before, *:after {
  box-sizing: border-box;
}

#vert_fs {
  display: flex;
}

#vert_fs > div {
  border: 1px solid green;
}

#fs_images {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#fs_images .frame_img {
  display: none;
}

#fs_images .frame_img.active {
  display: block;
  border: 1px dashed #999;
}

#vertical_filmstrip {
  border: 3px solid red;
  padding: 2rem;
  width: 50rem;
  margin: 1rem 7rem;
}

#vertical_filmstrip .slick-slide {
  background-color: wheat;
  height: 20rem;
}

#vertical_filmstrip .slick-center {
  border: 2px dashed blue;
}

#vertical_filmstrip {
  display: flex;
  flex-direction: row;
}

#vertical_filmstrip .vert-nav-dots {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#vertical_filmstrip .vert-nav-dots li.slick-active button {
  background-color: red;
  border: 1px solid red;
}

@media screen and (min-width: 300px) {
  #vert_fs {
    flex-direction: column;
  }
  #vertical_filmstrip {
    width: 80%;
    margin: 0;
  }
}
